.App {
    --header_height: 60px;
    font-family: Montserrat,sans-serif;
}

.hw {
    border-bottom: 1px solid #d9d9d9;
    border-top: 1px solid #d9d9d9;
    margin: 30px 0;
    padding: 32px 70px;
    transition: .2s ease-in-out;
    display: flex;
    flex-direction: column;
    gap: 10px;
}

.hwTitle {
    color: #000;
    font-size: 22px;
    font-style: normal;
    font-weight: 600;
    line-height: 27px;
    margin: 10px auto;
    max-width: 1280px;
    padding: 0 20px;
}

.loading {
    position: absolute;
    width: 51%;
    height: 60%;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: rgba(255, 255, 255, 0.7);
}